export default {
  components: {
    // toyoCard
  },
  data() {
    return {
      today: {
        m: '',
        d: '',
        day: ''
      },
      showClock: false,
      showPoster: false, // 打卡成功拟态框状态
      isPunch: false // 是否打卡
    };
  },
  onLoad() {
    this.getDate();
  },
  methods: {
    // 获取今日日期
    getDate() {
      let date = new Date();
      let m = date.getMonth() + 1; //获取当前月份(0-11,0代表1月)
      let d = date.getDate();
      let day = date.getDay();
      if (day === 0) day = '天';
      if (day === 1) day = '一';
      if (day === 2) day = '二';
      if (day === 3) day = '三';
      if (day === 4) day = '四';
      if (day === 5) day = '五';
      if (day === 6) day = '六';
      this.today.m = m;
      this.today.d = d;
      this.today.day = day;
    },
    // 立即打卡
    immediatelyClock() {
      this.showClock = false;
      this.showPoster = true;
    }
  }
};
